import echarts from 'echarts';
import config from './ChartBar.config.js';
const props = config.properties.reduce((p,n)=>{
	if(!p[n.props]){
		p[n.props] = {
			required:false,
			default:function(){
				if(n.type === 'group'){
					return n.group.reduce((p,n)=>{
				                if(!p[n.props]){
				                    p[n.props] = n.value;
				                }
				                return p;
				            },{});
				}
				return n['value'];
			}
		}
	}
	return p;
},{});
export default {
	name:'custom-chart-bar',
	inject:['componentWraper'],
	props:props,
	data(){
		return {

		}
	},
	computed:{
		options:function(){

		}
	},
	watch:{
		'$store.getters.selectedComponentInfo.config.config':{
			handler:function(value,oldValue){
				let options = this.chartInstance.getOption();
				options.xAxis[0].show = value.xAxis.show;
				options.xAxis[0].name = value.xAxis.name;
				this.chartInstance.setOption(options,true);
			},
			deep:true
		}
	},
	mounted(){
		console.log('props',props);
		var container = this.$refs.container;
		var chartInstance = echarts.init(container);
		container.dataset.chartInstance = chartInstance;
		this.chartInstance = chartInstance;
		chartInstance.setOption( {
								    color: ['#3398DB'],
								    tooltip : {
								        trigger: 'axis',
								        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
								            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
								        }
								    },
								    grid: {
								        left: '3%',
								        right: '4%',
								        bottom: '3%',
								        containLabel: true
								    },
								    xAxis : [
								        {
								            type : 'category',
								            data : ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
								            axisTick: {
								                alignWithLabel: true
								            }
								        }
								    ],
								    yAxis : [
								        {
								            type : 'value'
								        }
								    ],
								    series : [
								        {
								            name:'直接访问',
								            type:'bar',
								            barWidth: '60%',
								            data:[10, 52, 200, 334, 390, 330, 220]
								        }
								    ]
		});
		this.componentWraper.$on('onComponentResizing',function(size){
			chartInstance.resize(size);
		});
		this.$watch('$attrs.config.xAxis',function(value,oldValue){
			console.log('xAxisUpdate',value);
		},{deep:true});
	},
	updated(){
		console.log('chartUpdated');
	}
}