export default {
    name:'any-alert',
    props:{
        message:{
            type:String,
            required:true,
            default:'',
            description:'提示信息'
        },
        type:{
            type:String,
            required:false,
            default:'error',
            description:'提示类型,有效值(success|info|warning|error)'
        },
        icon:{
            type:String,
            required:false,
            default:'',
            description:'图标'
        },
        showIcon:{
            type:Boolean,
            required:false,
            default:false,
            description:'是否显示图标'
        }
    }
}