export default {
    name:"any-icon",
    props:{
        type:{
            type:String,
            required:true,
            default:'editer',
            description:'图标类型'
        },
        size:{
            type:String,
            required:false,
            default:'1em',
            description:'图标大小'
        },
        color:{
            type:String,
            required:false,
            default:'#000000',
            description:'图标颜色'
        },
        fontFamily:{
            type:String,
            required:false,
            default:'any-icon',
            description:'字体名称'
        },
        useSVG:{
            type:Boolean,
            required:false,
            default:false,
            description:'是否以svg格式渲染图标'
        }
    },
    data(){
        return {}
    },
    computed:{
        className:function(){
            return `${this.fontFamily}-${this.type}`;
        }
    },
    components:{
        
    }
}