let components = {

};
/**
 * @desc 判断组件配置项是否缓存
 * @param {String} type 组件类型
 * @return {Boolean} 是否缓存
 */
export function componentHasCache(type){
	return components[type] ? true : false;
}

/**
 * @desc 缓存组件配置项
 * @param {String} type 组件类型
 * @param {Object} config 组件配置项
 * @return {Undefined} 无返回值
 */
export function cacheComponentConfig(type,config){
	if(componentHasCache(type)){
		console.warn(`${type} 类型的组件已经缓存`);
		return;
	}
	components[type] = config;
}

/**
 * @desc 获取指定类型组件的配置项
 * @param {String} type 组件类型
 * @return {Object} 组件的配置项
 */
export function getComponentConfig(type){
	return components[type];
}
/**
 * @desc获取所有组件的配置项
 * @return {Object} 所有组件的配置项
 */
export function getAllComponentConfig(type){
	return components;
}