import CrisisgoIcon from '../Icon/Icon.component.vue';
export default {
    name:'any-button',
    props:{
        disabled:{
            type:Boolean,
            required:false,
            default:false,
            description:'按钮是否可用'
        },
        type:{
            type:String,
            required:false,
            default:'primary',
            description:'按钮类型'
        },
        icon:{
            type:String,
            required:false,
            default:'',
            description:'按钮'
        }
    },
    data(){
        return {}
    },
    computed:{
        typeClass(){
            return `button-${this.type}`
        }
    },
    components:{
        CrisisgoIcon
    }

}