import echarts from 'echarts';
import config from './ChartLine.config.js';
import eventBus from '@/utils/eventBus.js'
const props = config.properties.reduce((p,n)=>{
	if(!p[n.props]){
		p[n.props] = {
			required:false,
			default:function(){
				return n['value'];
			}
		}
	}
	return p;
},{});
export default {
	name:'custom-chart-line',
	inject:['componentWraper'],
	props:props,
	data(){
		return {

		}
	},
	computed:{
		options:function(){

		}
	},
	mounted(){
		var container = this.$refs.container;
		var chartInstance = echarts.init(container);
		container.dataset.chartInstance = chartInstance;
		chartInstance.setOption( {
								    color: ['#3398DB'],
								    tooltip : {
								        trigger: 'axis',
								        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
								            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
								        }
								    },
								    grid: {
								        left: '3%',
								        right: '4%',
								        bottom: '3%',
								        containLabel: true
								    },
								    xAxis : [
								        {
								            type : 'category',
								            data : ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
								            axisTick: {
								                alignWithLabel: true
								            }
								        }
								    ],
								    yAxis : [
								        {
								            type : 'value'
								        }
								    ],
								    series : [
								        {
								            name:'直接访问',
								            type:'line',
								            barWidth: '60%',
								            data:[10, 52, 200, 334, 390, 330, 220]
								        }
								    ]
		});
		this.componentWraper.$on('onComponentResizing',function(size){
			chartInstance.resize(size);
		});
	},
	watch:{
		'$parent.width':function(val,old){
			console.log('width-change',val,old);
		}
	}
}