/**
 * 验证手机号码
 * @param {String} string 需要验证的字符
 * @param {Number} length 长度
 */
export function mobilePhone(string,length=11){
    let reg = /^\d+$/;
    if(!reg.test(string.trim())){
        return false;
    }
    if(string.length !== length){
        return false;
    }
    return true;
}