export default {
    name:'any-form-item',
    provide(){
        return {
            formItemInstance:this
        }
    },
    inject:['formInstance'],
    props:{
        prop:{
            type:String,
            required:false,
            default:'',
            description:'绑定的表单属性'
        },
        required:{
            type:Boolean,
            required:false,
            default:false,
            description:'表单项是否必填'
        },
        label:{
            type:String,
            required:false,
            default:'',
            description:'lable字符'
        },
        labelWidth:{
            type:Number,
            required:false,
            default:100,
            description:'label字符宽度'
        },
        labelTextAlign:{
            type:String,
            required:false,
            default:'right',
            description:'label字体对齐方式,可选值为text-align的有效值'
        },
        labelAlignItems:{
            type:String,
            required:false,
            default:'center',
            description:'label上面对齐方式,可选值为flex布局align-items的有效值'
        }
    },
    data(){
        return {
            
        }
    },
    computed:{
        labelStyle(){
            return {
                width:this.labelWidth+'px',
                textAlign:this.labelTextAlign
            }
        }
    },
    methods:{
        reset(){
            this.value = ''
        }
    },
    mounted(){
        this.formInstance.$on('reset',()=>{
            this.reset();
        })
    }
}