
import * as validates from '@/utils/validates.js';
export default {
    name:'any-form',
    provide(){
        return {
            formInstance:this
        }
    },
    props:{
        model:{
            type:Object,
            required:false,
            default(){
                return {}
            }
        },
        rules:{
            type:Object,
            required:false,
            default(){
                return {}
            }
        },
        disabled:{
            type:Boolean,
            required:false,
            default:false
        },
        labalWidth:{
            type:Number,
            required:false,
            default:100
        }
    },
    data(){
        return {}
    },
    methods:{
        /**
         * 验证规则
         * @returns {Promise} 
         */
        validate(){
            return new Promise((resolve,reject)=>{
                let valid = Object.keys(this.rules).every(key=>{
                    return this.rules[key].every(rule=>{
                        if(typeof validates[rule.type] === 'function'){
                            return validates[rule.type](this.model[key],rule.min,rule.max);
                        }
                    });
                });
                if(valid){
                    resolve(true);
                }else{
                    this.$emit('validate-error');
                    reject(false);
                }
            })
        },
        /**
         * 重置表单(有默认值的设回默认值，没有默认值的设空)
         * @returns {undefined} 无返回值
         */
        reset(){
            this.$el.reset();
            this.$emit('reset');
        },
        /**
         * 清空表单
         * @returns {undefined} 无返回值
         */
        clear(){
            this.$emit('clear');
        }
    }
}
