import echarts from 'echarts';
import config from './ChartPie.config.js';
const props = config.properties.reduce((p, n) => {
    if (!p[n.props]) {
        p[n.props] = {
            required: false,
            default: function() {
                return n['value'];
            }
        }
    }
    return p;
}, {});
export default {
    name: 'custom-chart-pie',
    inject:['componentWraper'],
    props: props,
    data() {
        return {}
    },
    computed: {
        options: function() {}
    },
    mounted() {
        var container = this.$refs.container;
        var chartInstance = echarts.init(container);
        container.dataset.chartInstance = chartInstance;
        chartInstance.setOption({
            title: {
                text: '某站点用户访问来源',
                subtext: '纯属虚构',
                x: 'center'
            },
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            legend: {
                orient: 'vertical',
                left: 'left',
                data: ['直接访问', '邮件营销', '联盟广告', '视频广告', '搜索引擎']
            },
            series: [{
                name: '访问来源',
                type: 'pie',
                radius: '55%',
                center: ['50%', '60%'],
                data: [{
                    value: 335,
                    name: '直接访问'
                }, {
                    value: 310,
                    name: '邮件营销'
                }, {
                    value: 234,
                    name: '联盟广告'
                }, {
                    value: 135,
                    name: '视频广告'
                }, {
                    value: 1548,
                    name: '搜索引擎'
                }],
                itemStyle: {
                    emphasis: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        });
        this.componentWraper.$on('onComponentResizing',function(size){
            chartInstance.resize(size);
        });
    },
    watch: {
        '$parent.width': function(val, old) {
            console.log('width-change', val, old);
        }
    }
}