import config from './Image.config.js';
const props = config.properties.reduce((p,n)=>{
	if(!p[n.props]){
		p[n.props] = {
			required:false,
			default:function(){
				return n['value'];
			}
		}
	}
	return p;
},{});
export default {
	name:'custom-image',
	props:props,
	data(){
		return {

		}
	}
}