<template>
  <div id="container">
    <div id="componentList" @selectstart=onSelectStart>
       <ComponentList />
    </div>
    <div 
        id="layoutDesigner"
        ref="layoutDesigner"
    >
      <!-- 当前选中组件的X轴参考线 -->
      <div id="selected-components-refer-x"
        v-if="$store.getters.selectedComponentInfo.show"
        :style="{left:$store.getters.selectedComponentInfo.config.left*$store.state.scale+'px'}"
      ></div>
      <!-- 当前选中组件的Y轴参考线 -->
      <div id="selected-components-refer-y"
        v-if="$store.getters.selectedComponentInfo.show"
        :style="{top:$store.getters.selectedComponentInfo.config.top*$store.state.scale+'px'}" 
      ></div>
      <div id="selected-components-refer-position"
        v-if="$store.getters.selectedComponentInfo.show"
        :key="$store.getters.selectedComponentInfo.config.id"
        :style="{top:$store.getters.selectedComponentInfo.config.top*$store.state.scale+'px',left:$store.getters.selectedComponentInfo.config.left*$store.state.scale+'px'}"
      >(<span>{{$store.getters.selectedComponentInfo.config.left}}</span>, <span>{{$store.getters.selectedComponentInfo.config.top}}</span>)</div>
      
      <div
        id="designerCanvas" 
        ref="designerCanvas"
        tabindex="10000" 
        :style="{
                  width:$store.state.canvas.width + 'px',
                  height:$store.state.canvas.height + 'px',
                  transform:'scale('+$store.state.scale+')'
        }"
        @blur=onBlur
        @focus=onFocus
        @mouseenter=onMouseEnter 
        @mouseleave=onMouseLeave 
        @mousedown=onMousedown
        @mouseup=onMouseup
        @mousemove=onMousemove
        @contextmenu=onContextmenu
        >
            <div
                class="select-helper" 
                v-if="selectHelper.show"
                :style="{
                          zIndex:$store.state.components.length+2,
                          left:selectHelper.left+'px',
                          top:selectHelper.top+'px',
                          width:selectHelper.width+'px',
                          height:selectHelper.height+'px'
                        }"
            ></div>
            <div
                ref="group"
                class="select-group"
                v-if="$store.getters.selectedGroup.show"
                :style="{
                  zIndex:$store.state.components.length+3,
                  left:$store.getters.selectedGroup.left+'px',
                  top:$store.getters.selectedGroup.top+'px',
                  width:$store.getters.selectedGroup.width+'px',
                  height:$store.getters.selectedGroup.height+'px'
                }"
                @mousedown=onGroupMousedown
            >
              <div style="width: 100%;height: 100%;">
                <!-- <div class="resize-handle" style="left:-4px;top:-4px;cursor: nwse-resize;"
                ></div>
                <div class="resize-handle" style="left:-4px;bottom:-4px;cursor: nesw-resize;"
                ></div>
                <div class="resize-handle" style="right:-4px;top:-4px;cursor: nesw-resize;"
                ></div> -->
                <div class="resize-handle" style="right:-4px;bottom:-4px;cursor: nwse-resize;"
                  @mousedown.stop=startResizeGroup
                ></div>
              </div>
            </div>
            <div
              ref="referenceLine"
              class="reference-line"
              v-if="$store.state.referenceLine.show"
              :style="{
                zIndex:$store.state.components.length+4,
              }"
            >
              <div style="width: 100%;height: 100%;background-color: transparent;">
                <div class="reference-line-x">
                  <div v-for="line in $store.state.referenceLine.referenceLineX" :key="line">
                    
                  </div>
                </div>
                <div class="reference-line-y">
                  <div v-for="line in $store.state.referenceLine.referenceLineY" :key="line">
                    
                  </div>
                </div>
              </div>
            </div>      
            <ComponentWraper
                v-for="component in components" 
                :key="component.id"
                :type="component.type"
                :id="component.id"
                :width="component.width" 
                :height="component.height" 
                :top="component.top" 
                :left="component.left"
                :locked="component.locked"
                :visible="component.visible" 
                :backgroundColor="component.backgroundColor"
                :config="component.config" 
            />
      </div>
    </div>

    
    <div id="layerInfo" style="display: flex;flex-wrap: wrap;overflow: hidden;">
        <div id="componentInfo" style="height: 300px;">
          <h3 class="panel-title">属性</h3>
          <div v-if="selectedComponentInfo.show" style="height: calc(100% - 32px);overflow-y: scroll;padding-left: 1em;">
             <div v-if="key!=='config'" v-for="(val,key) in selectedComponentInfoList" :key="key">
               <label for="" style="color: #ffffff;padding-right: 0.5em;">{{key}}</label>
               <input type="text" v-model.lazy="selectedComponentInfoList[key]">
             </div>
             <div v-for="(val,key) in selectedComponentInfoList['config']" :key="key">
               <template v-if="typeof(val) === 'object'">
                  <h4>{{key}}</h4>
                  <div v-for="(subVal,subKey) in val" :key="subKey">
                    <label for="">{{subKey}}</label>
                    <input type="text" v-model.lazy="val[subKey]">
                  </div>
               </template>
               <template v-else>
                 <label for="" style="color: #ffffff;padding-right: 0.5em;">{{key}}</label>
                 <input type="text" v-model.lazy="selectedComponentInfoList['config'][key]">
               </template>
             </div>
          </div>
        </div>
        <div id="componentLayers" style="width:100%;height: calc(100% - 300px)">
          <h3 class="panel-title">图层(总数:{{$store.getters.layer_length}})</h3>
          <div style="height:calc(100% - 32px);overflow: hidden auto;">
            <ul class="layer-list" style="font-size: 12px;color:#ffffff;text-indent: 0;">
              <li class="layer-list-item" 
                  :key="component.id"
                  :class="{active:$store.getters.selectedComponentInfo.index===idx}" 
                  v-for="(component,idx) in $store.state.components" 
                  style="height: 32px;line-height: 32px;padding-left: 2em;"
                  @click="selectedLayer(idx,component.id)"
              >
                <span v-if="!component.rename"
                  @dblclick="component.rename = true"
                >
                  {{component.name}}
                </span>
                <input 
                  v-if="component.rename" 
                  v-model.lazy="component.name"
                  style="height: 100%;width: 100%;border:none;" 
                  @blur="component.rename = false" 
                />
              </li>
            </ul>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import ComponentList from './components/platform/componentList/component.vue';
import ComponentWraper from './components/platform/componentWraper/component.vue';
import { mapState,mapGetters } from 'vuex'
import throttle from 'lodash/throttle'
import { translateSelectedComponents,endTranslateSelectedComponents,resizeSelectedComponents,emptySelectedComponents } from '@/services/selectedComponentElements.js';
import {getOffset2body,config2props} from './utils';
import {hasCloneElem,addMouseupCallBack} from '@/services/cloneElem2body';
import {getComponentConfig} from '@/services/componentConfig';


export default {
  name: "app",
  components: {
		ComponentWraper,
    ComponentList
  },
  data(){
    return {
        inEdit:false,
        hasPointerInDesigner:false,
        selectHelper:{
          show:false,
          left:0,
          top:0,
          width:0,
          height:0
        },
        startPosition:{
          left:0,
          top:0
        },
        isComponentResizing:false,
        componentResizeStartPosition:{
          left:0,
          top:0
        },
        isGroupDraging:false,//组选框是否处于拖拽中
        groupStartPosition:{
          left:0,
          top:0
        },
        isGroupResizing:false, //组选框是否可处于改变尺寸中
        groupReseRate:1,
        groupResizeStartPosition:{
          left:0,
          top:0
        },
        layerActive:0,

    }
  },
  computed:mapState({
    components:state=>state.components,
    ...mapGetters({
      selectedComponentInfo:'selectedComponentInfo'
    }),
    selectedComponentInfoList:function(){
      return this.selectedComponentInfo.config
    }
  }),
  methods:{
      onFocus(){
        this.inEdit = true;
        window.addEventListener('keydown',this.onKeydown,{
          passive:false
        });
      },
      onBlur(event){
        this.inEdit = false;
        window.removeEventListener('keydown',this.onKeydown,{
          passive:false
        });
      },
      /**
       * @desc 添加组件
       */
      onAddComponent(position,type){
        if(!this.hasPointerInDesigner){
          return;
        }
        let offset = getOffset2body(this.$refs.designerCanvas);
        let {left,top} = offset;
        let config = getComponentConfig(type);
        this.$store.commit('ADD_COMPONENT',{
                width: config.size.width,
                height: config.size.height,
                top: position.pageY - top　+ this.$refs.layoutDesigner.scrollTop,
                left: position.pageX - left + + this.$refs.layoutDesigner.scrollLeft,
                type,
                config:config2props(config.properties)
        });
      },
      onSelectStart(event){
        event.returnValue = false;
        return false;
      },
      onKeydown(e){
        if(e.ctrlKey === true && e.key.toLocaleLowerCase() === 'r'){
          e.preventDefault();
        }
        if(e.ctrlKey === true && e.key.toLocaleLowerCase() === 'a'){
          e.preventDefault();
          this.$store.commit('SELECTED_ALL_COMPONENTS');
        }
        if(e.key.toLocaleLowerCase() === 'delete'){
          this.$store.commit('DELETE_COMPONENT');
        }
        if(e.ctrlKey === true && e.key.toLocaleLowerCase() ==='c'){
          this.$store.commit('COPY_COMPONENTS');
        }
        if(e.ctrlKey === true && e.key.toLocaleLowerCase() ==='v'){
          navigator.clipboard.read()
          .then(file => {
            console.log('Pasted file: ', file);
          })
          .catch(err=>{
            debugger;
          });
          this.$store.commit('PASTE_COMPONENTS');
        }
        if(e.ctrlKey === true && e.key.toLocaleLowerCase() ==='z'){
          this.$store.commit('CTRL_Z');
        }
        if(e.ctrlKey === true && e.key.toLocaleLowerCase() ==='y'){
          this.$store.commit('CTRL_Y');
        }
        if(e.key.toLocaleLowerCase() === 'arrowup' && e.shiftKey===false){
          e.preventDefault();
          this.$store.commit('MOVE_SELECTED_COMPONENTS_TOP');
        }
        if(e.key.toLocaleLowerCase() === 'arrowdown' && e.shiftKey===false){
          e.preventDefault();
          this.$store.commit('MOVE_SELECTED_COMPONENTS_BOTTOM');
        }
        if(e.key.toLocaleLowerCase() === 'arrowright' && e.shiftKey===false){
          e.preventDefault();
          this.$store.commit('MOVE_SELECTED_COMPONENTS_RIGHT');
        }
        if(e.key.toLocaleLowerCase() === 'arrowleft' && e.shiftKey===false){
          e.preventDefault();
          this.$store.commit('MOVE_SELECTED_COMPONENTS_LEFT');
        }
        if(e.ctrlKey === true && e.shiftKey === true && e.key.toLocaleLowerCase() === 'arrowleft'){
          this.$store.commit('ALIGN_SELECTED_COMPONENTS_LEFT');
        }
        if(e.ctrlKey === true && e.shiftKey === true && e.key.toLocaleLowerCase() === 'arrowright'){
          this.$store.commit('ALIGN_SELECTED_COMPONENTS_RIGHT');
        }
        if(e.ctrlKey === true && e.shiftKey === true && e.key.toLocaleLowerCase() === 'arrowup'){
          this.$store.commit('ALIGN_SELECTED_COMPONENTS_TOP');
        }
        if(e.ctrlKey === true && e.shiftKey === true && e.key.toLocaleLowerCase() === 'arrowdown'){
          this.$store.commit('ALIGN_SELECTED_COMPONENTS_BOTTOM');
        }
        if(e.ctrlKey === true && e.shiftKey === true && e.key.toLocaleLowerCase() === 'l'){
          this.$store.commit('UNLOCK_SELECTED_COMPONENTS');
        }else if(e.ctrlKey === true && e.key.toLocaleLowerCase() === 'l'){
          e.preventDefault();
          this.$store.commit('LOCK_SELECTED_COMPONENTS');
        }
        if(e.ctrlKey === true && e.shiftKey === true && e.key.toLocaleLowerCase() === 'g'){
          e.preventDefault();
          this.$store.commit('UNGROUP_COMPONENTS');
        }else if(e.ctrlKey === true && e.key.toLocaleLowerCase() === 'g'){
          e.preventDefault();
          this.$store.commit('GROUP_COMPONENTS');
        }
        if(e.ctrlKey === true && e.shiftKey ===true && e.key.toLocaleLowerCase() === 'h'){
          e.preventDefault();
          this.$store.commit('SHOW_SELECTED_COMPONENTS');
        }else if(e.ctrlKey === true && e.key.toLocaleLowerCase() === 'h'){
          e.preventDefault();
          this.$store.commit('HIDE_SELECTED_COMPONENTS');
        }else if(e.ctrlKey === true && e.key.toLocaleLowerCase() === '-'){
          e.preventDefault();
          this.$store.commit('SET_CANVAS_SCALE_NARROW');
        }else if(e.ctrlKey === true && e.key.toLocaleLowerCase() === '='){
          e.preventDefault();
          this.$store.commit('SET_CANVAS_SCALE_ENLARGE');
        }else if(e.ctrlKey === true && e.key.toLocaleLowerCase() === '0'){
          // e.preventDefault();
          this.$store.commit('RESET_CANVAS_SCALE');
        }else if(e.ctrlKey === true && e.key.toLocaleLowerCase() === ' '){
          e.preventDefault();
          this.$store.commit('SET_CANVAS_SCALE_ENLARGE');
        }else if(e.altKey === true && e.key.toLocaleLowerCase() === ' '){
          e.preventDefault();
          this.$store.commit('SET_CANVAS_SCALE_NARROW');
        }
        
      },
      onKeyup(e){
        if(e.ctrlKey === true && this.$store.state.ctrl === false){
          // this.$store.commit('SET_KEY_CTRL_TRUE');
        }else if(e.ctrlKey === false && this.$store.state.ctrl === true){
          // this.$store.commit('SET_KEY_CTRL_FALSE');
        }else if(e.shiftKey === true && this.$store.state.shift === false){
          // this.$store.commit('SET_KEY_SHIFT_TRUE');
        }else if(e.shiftKey === false && this.$store.state.shift === true){
          // this.$store.commit('SET_KEY_SHIFT_FALSE');
        }else if(e.ctrlKey === true && e.key.toLocaleLowerCase() === 'a'){
          e.preventDefault();
          this.$store.commit('SELECTED_ALL_COMPONENTS');
        }else if(e.key.toLocaleLowerCase() === 'delete'){
          this.$store.commit('DELETE_COMPONENT');
        }
        
      },
      onMouseEnter(e){
          this.hasPointerInDesigner = true;
      },
      onMouseLeave(e){
          this.hasPointerInDesigner = false;
      },
      onMousedown(e){
        if(e.button !== 0){
          e.stopPropagation();
          e.preventDefault();
          return;
        }
        let offset = getOffset2body(this.$refs.designerCanvas);
        let {left,top} = offset;
        this.startPosition.left = this.selectHelper.left =  e.pageX;
        this.startPosition.top = this.selectHelper.top = e.pageY + this.$refs.designerCanvas.scrollTop;
        this.showSelectedHelper();
        if(this.$store.state.selectedIds.length>0){
          this.$store.commit('EMPTY_SELECTED_COMPONENTS');
        }
      },
      onMouseup(e){
        if(e.button !== 0){
          e.stopPropagation();
          e.preventDefault();
          return;
        }
        let translateX = e.pageX - this.groupStartPosition.left;
        let translateY = e.pageY - this.groupStartPosition.top;
        this.hideSelectedHelper();
        if(this.isGroupDraging){
          this.isGroupDraging = false;
          if(translateX === 0 && translateY === 0){
            void 0;
          }else{
            this.$store.commit('TRANSLATE_SELECTED_COMPONENTS',{
              translateX:translateX/this.$store.state.scale,
              translateY:translateY/this.$store.state.scale
            });
          }
          this.$refs.group.style.transform = 'translate(0,0)';
          endTranslateSelectedComponents();
        }
        if(this.isGroupResizing){
          this.isGroupResizing = false;
          this.$store.commit('SCALE_SELECTED_COMPONENTS',this.groupReseRate);
          this.groupReseRate = 1;
        }
        if(this.isComponentResizing){
          this.isComponentResizing = false;
          this.$store.commit('SCALE_SELECTED_COMPONENTS',this.groupReseRate);
          this.groupReseRate = 1;
        }
      },
      scaleSelectHelper(){
        this.selectHelper.top /= this.$store.state.scale;
        this.selectHelper.left /= this.$store.state.scale;
        this.selectHelper.width /= this.$store.state.scale;
        this.selectHelper.height /= this.$store.state.scale;
      },
      onMousemove(e){
        e.preventDefault();
        //更新框选容器
        if(this.selectHelper.show){
          let offset = getOffset2body(this.$refs.designerCanvas);
          let {left,top} = offset;
          if(e.pageX > this.startPosition.left){
            let translateX = e.pageX - this.startPosition.left;
            this.selectHelper.left = this.startPosition.left - left;
            this.selectHelper.width = e.pageX - this.startPosition.left;
          }else{
            let translateX = e.pageX - this.startPosition.left;
            this.selectHelper.left = this.startPosition.left + translateX - left;
            this.selectHelper.width = this.startPosition.left - e.pageX;
          }
          if((e.pageY+this.$refs.layoutDesigner.scrollTop) > this.startPosition.top){
            this.selectHelper.top = this.startPosition.top - top;
            this.selectHelper.height = e.pageY + this.$refs.layoutDesigner.scrollTop - this.startPosition.top;
          }else{
            this.selectHelper.top = e.pageY + this.$refs.layoutDesigner.scrollTop - top;
            this.selectHelper.height = this.startPosition.top - e.pageY - this.$refs.layoutDesigner.scrollTop;
          }
          this.scaleSelectHelper();
          this.$store.commit('SELECTED_COMPONENTS_BY_HELPER',this.selectHelper);
        }
        if(this.isGroupDraging && this.$store.state.selectedIds.length>0){
          let {left,top} = this.groupStartPosition;
          let translateX = e.pageX - this.groupStartPosition.left;
          let translateY = e.pageY - this.groupStartPosition.top;
          this.$refs.group.style.transform = `translate(${translateX/this.$store.state.scale}px,${translateY/this.$store.state.scale}px)`;
          translateSelectedComponents(translateX/this.$store.state.scale,translateY/this.$store.state.scale,{left,top});
        }
        if(this.isGroupResizing){
          let translateX = e.pageX - this.groupResizeStartPosition.left;
          if( (translateX+this.$store.getters.selectedGroup.width) <= 1 ){
            return;
          }
          let rate = (this.$store.getters.selectedGroup.width+translateX)/this.$store.getters.selectedGroup.width;
          this.groupReseRate = rate;
          this.$refs.group.style.width = `${Math.floor(this.$store.getters.selectedGroup.width*rate)}px`;
          this.$refs.group.style.height = `${Math.floor(this.$store.getters.selectedGroup.height*rate)}px`;
          resizeSelectedComponents(rate);
        }
        if(this.isComponentResizing){
          let translateX = e.pageX - this.componentResizeStartPosition.left;
          let rate = (this.$store.getters.selectedGroup.width+translateX)/this.$store.getters.selectedGroup.width;
        }

      },
      onGroupMousedown(e){
        if(e.button !== 0){
          return;
        }
        e.stopPropagation();
        this.isGroupDraging = true;
        this.componentResizeStartPosition.left = e.pageX;
        this.componentResizeStartPosition.top = e.pageY;
        this.groupStartPosition.left = e.pageX;
        this.groupStartPosition.top = e.pageY;
      },
      /**
       * @desc 屏蔽鼠标右键
       */
      onContextmenu(e){
        e.preventDefault();
      },
      /**
       *
       */
      showSelectedHelper(){
        this.selectHelper.show = true;
      },
      hideSelectedHelper(){
        this.selectHelper.show = false;
        this.selectHelper.width = 0;
        this.selectHelper.height = 0;
      },
      /**
       * @desc 开始组选框尺寸改变
       */
      startResizeGroup(e){
        this.isGroupResizing = true;
        this.groupResizeStartPosition.left = e.pageX;
        this.groupResizeStartPosition.top = e.pageY;
      },
      /**
       * @desc 
       */
      startResizeComponent(e){
        this.isComponentResizing = true;
        this.componentResizeStartPosition.left = e.pageX;
        this.componentResizeStartPosition.top = e.pageY;
      },
      /**
       * @desc 图层面板选中组件
       * @param {Number} idx 图层索引
       * @param {String} componentId 组件ID
       */
      selectedLayer(idx,componentId){
        this.$store.getters.selectedComponentInfo.index = idx;
        this.$store.commit('RESET_SELECTED_COMPONENTS',{
          id:componentId
        });
      },
      getComponentConfig(type,prop){
        return getComponentConfig(type)['properties'].find(property=>property.props === prop);
      },
      typeof(object){
        return typeof object
      },
      preventMousewheel(){
        window.addEventListener('mousewheel',event=>{
          if(event.ctrlKey === true){
            event.preventDefault();
          }
        },{passive:false});
      }
  },
  watch:{
    
  },
  mounted(){
    this.preventMousewheel();
    addMouseupCallBack(this.onAddComponent);
    // window.addEventListener('click',()=>{
    //   this.inEdit = false;
    // });
    // window.addEventListener('keydown',this.onKeydown,{
    //   passive:false
    // });
  },
  destroy(){
    window.removeEventListener('keydown',this.onKeydown);
    window.removeEventListener('keyup',this.onKeyup);
  }
};
</script>

<style>
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -moz-user-select: none;
}
html,
body {
  width: 100%;
  height: 100%;
}
#container {
  display: flex;
  width: 100%;
  height: 100%;
}
#componentList {
  width: 320px;
  height: 100%;
  background-color: rgb(68, 68, 68);
  color: #ffffff;
}
#designer-toolbar{
  width: 100%;
  height: 100px;
  background-color: #cdcdcd;
}
#layoutDesigner {
  position: relative;
  overflow: auto;
  /*width: 1920px;*/
  flex: 1;
  height: 100%;
  background-image: url(data:image/png;base64,);
  background-size: 16px 16px;
  background-repeat: repeat;
}
#designerCanvas{
  transform-origin: left top;
  width:100%;
  height: auto;
  min-height: 100%;
  overflow: hidden;
}
#selected-components-refer-x,#selected-components-refer-y{
  position: absolute;
  background-color: transparent;
  border:.5px dashed #ff0000;
  pointer-events: none;
}
#selected-components-refer-x{
  height: 100%;
  width: 1px;
  transform: translateX(-2px);
}
#selected-components-refer-y{
  width: 100%;
  height: 1px;
  transform: translateY(-2px);
}
#selected-components-refer-position{
  position: absolute;
  pointer-events: none;
  transform: translate(calc(-100% - 5px), calc(-100% - 5px));
  color: #ff0000;
}

#layerInfo {
  height: 100%;
  width: 320px;
  background-color: rgb(68, 68, 68);
}
#componentInfo {
  width: 100%;
}
.selected-component {
  position: absolute;
  background-color: red;
  outline: 2px solid blue;
  box-sizing: content-box;
  cursor: move;
}
.resize-handle {
  position: absolute;
  width: 9px;
  height: 9px;
  border: 1px solid #000000;
  border-radius: 50%;
  background-color: #ffffff;
}
.reference-line {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.layer-list-item.active {
  background-color: #3899ec;
}
.panel-title {
  background-color: #000000;
  height: 32px;
  width: 100%;
  line-height: 32px;
  font-size: 14px;
  text-indent: 2em;
  color: #ffffff;
}
</style>
