
import * as monaco from 'monaco-editor';
export default {
  name: 'code-editor',
  props:{
    language:{
      type:String,
      require:false,
      default:'typescript'
    },
    value:{
      type:String,
      require:true,
      default:`/**
  * 
  *
  *
  *
**/`
    },
    theme:{
      type:String,
      require:false,
      default:'vs-dark'
    }
  },
  mounted(){
    let model =  monaco.editor.createModel('console.log("hi")', 'javascript');
    this.editor = monaco.editor.create(this.$el, {
      value: this.value,
      language:this.language,
      mode: model,
      theme: this.theme
    });
  }
}