import config from './Text.config.js';
const props = config.properties.reduce((p,n)=>{
	if(!p[n.props]){
		p[n.props] = {
			required:false,
			default:function(){
				return n['value'];
			}
		}
	}
	return p;
},{});
export default {
	name:'custom-text',
	props:props,
	inject:['componentWraper'],
	data(){
		return {
			editable:false
		}
	},
	computed:{
		textShadow:function(){
			let isTextShadow = null;
			if(typeof this.isTextShadow === 'boolean'){
				isTextShadow = this.isTextShadow;
			}else if(typeof this.isTextShadow === 'string'){
				isTextShadow = this.isTextShadow === 'true' ? true : false; 
			}
			return isTextShadow ? `${this.textShadowColor} 0 0 ${this.textShadowSize}px` : 'none';
		},
		boxShadow:function(){
			let isBoxShadow = null;
			if(typeof this.isBoxShadow === 'boolean'){
				isBoxShadow = this.isBoxShadow;
			}else if(typeof this.isBoxShadow === 'string'){
				isBoxShadow = this.isBoxShadow === 'true' ? true : false;
			}
			return isBoxShadow ? `${this.boxShadowColor} 0 0 ${this.boxShadowSize}px` : 'none';
		},
	},
	watch:{
		content:function(value,oldValue){
			this.$store.commit('SET_COMPONENT_PROPS',{content:value});
		}
	},
	methods:{
		onKeyEnter(e){
			this.$refs['edit-input'].blur();
			this.onCancelEdit();
		},
		onCancelEdit(e){
			this.editable = false;
			if(this.componentWraper){
				this.componentWraper.$emit('onCancelEdit');
			}
		}
	},
	mounted(){
		if(this.componentWraper){
			this.componentWraper.$on('onEdit',e=>{
				this.editable = true;
				setTimeout(()=>{
					this.$refs['edit-input'].focus();
				});
				
			});
		}
	},
	destroyed(){

	}
}