import Vue from 'vue';
import DynamicComponent from '../dynamicComponent/dynamic.script.js';
import {isNumberic} from '@/utils';
import debounce from 'lodash.debounce';
import {
    setStartPostion,
    toggleSelectedComponent,
    deleteSelectedComponent,
    translateSelectedComponents
} from '@/services/selectedComponentElements.js';
export default {
    name: 'component-wraper',
    props: {
        type: {
            type: String,
            default: 'text'
        },
        id: {
            type: String,
            required: true,
        },
        locked: {
            type: Boolean,
            required: true,
        },
        width: {
            // type: Number,
            required: true,
            validator: isNumberic
        },
        height: {
            // type: Number,
            required: true,
            validator: isNumberic
        },
        left: {
            // type: Number,
            required: true,
            validator: isNumberic
        },
        top: {
            // type: Number,
            required: true,
            validator: isNumberic
        },
        backgroundColor: {
            type: [String, Number],
            required: true,
            // 			validator:(value)=>{
            // 				if(typeof value === 'number'){
            // 					if(value < 0 || value > 16581375){
            // 						return false;
            // 					}else{
            // 						return true;
            // 					}
            // 				}else if(typeof value === 'string'){
            // 					if(value.indexOf('#') !== 0){
            // 						
            // 					}
            // 				}
            // 			}
        },
        visible: {
            type: Boolean,
            required: true
        },
        config:{
        	type:Object,
        	required:true
        }
    },
    components: {
        DynamicComponent
    },
    provide:function(){
        return {
            componentWraper:this
        }
    },
    data() {
        return {
            inDragging: false,
            inResizing: false,
            draggingTimer: null,
            inResizing: false,
            editable:false
        }
    },
    methods: {
        /**
         * @desc 鼠标双击(用于文本快捷输入等操作)
         * @param {*} e 事件对象
         */
        onDblClick(e){
            this.editable = true;
            this.$emit('onEdit',e);
        },
    	/**
    	 * @desc 鼠标选中组件并注册移动和按下事件
    	 * @param {Event} e 事件对象
    	 */
        onMouseDown(e) {
            if (e.button !== 0) {
                return;
            }
            if (e.ctrlKey === true) { //不加locked条件用于批量解锁
                this.$store.commit('TOGGLE_SELECTED_COMPONENTS', {
                    id: this.id
                });
            } else if (!e.ctrlKey && this.isSelected) {
                // this.$store.commit('RESET_SELECTED_COMPONENTS',{
                // 	id:this.id
                // });
            } else if (!e.ctrlKey && !this.isSelected) {
                this.$store.commit('RESET_SELECTED_COMPONENTS', {
                    id: this.id
                });
            }
            e.stopPropagation();
            if (!this.locked) {
                this.inDragging = true;
                this.inResizing = false;
            }
            this.position = {
                pageX: e.pageX,
                pageY: e.pageY
            };
            setStartPostion(e);
            window.addEventListener('mousemove', this.onDragging);
            window.removeEventListener('mouseup', this.onMouseUp);
            window.addEventListener('mouseup', this.onMouseUp);
            if (e.target === this.$el) {
                e.preventDefault();
                e.stopPropagation();
            }
        },
        /**
         * @desc 鼠标抬起结束拖拽并注销移动事件
         * @param {Event} e 事件对象
         */
        onMouseUp(e) {
            if (!this.inDragging || this.locked) {
                return;
            }
            let pageX = e.pageX;
            let pageY = e.pageY;
            let translateX = pageX - this.position.pageX;
            let translateY = pageY - this.position.pageY;
            this.$el.style.transform = `translate(${0}px,${0}px)`;
            this.inDragging = false;
            this.position = {
                pageX: 0,
                pageY: 0
            };
            window.removeEventListener('mousemove', this.onDragging);
            if (translateX === 0 && translateY === 0) {
                return;
            }
            this.$store.commit('TRANSLATE_SELECTED_COMPONENTS', {
                translateX,
                translateY
            });
        },
        /**
         * @desc 拖动元素
         * @param {Event} e 事件对象
         */
        onDragging(e) {
            if (!this.isSelected || !this.inDragging || this.locked) {
                return;
            } else {
                let {
                    left,
                    top
                } = this;
                let pageX = e.pageX;
                let pageY = e.pageY;
                let translateX = pageX - this.position.pageX;
                let translateY = pageY - this.position.pageY;
                translateSelectedComponents(translateX, translateY, {
                    left,
                    top
                });

                let styleLeft = (left + translateX) * this.$store.state.scale + 'px';
                let styleTop = (top + translateY) * this.$store.state.scale + 'px';
                document.querySelector('#selected-components-refer-x').style.left = styleLeft;
                document.querySelector('#selected-components-refer-y').style.top = styleTop;
                document.querySelector('#selected-components-refer-position').style.left = styleLeft;
                document.querySelector('#selected-components-refer-position').style.top = styleTop;
                document.querySelector('#selected-components-refer-position').innerHTML = `(${left + translateX}, ${top + translateY})`;
            }
        },
        /**
         * #desc 设置改变尺寸开始
         */
        onResizeStart() {
            this.inResizing = true;
            this.inDragging = false;
        },
        /**
         * @desc 开始改变尺寸
         * @param ｛Event｝e 事件对象
         * @returns {undefined} 无返回值
         */
        beginResize(e, dirction) {
            this.resizingDirection = dirction;
            this.resizeStartPosition.left = e.pageX;
            this.resizeStartPosition.top = e.pageY;
            this.inResizing = true;
            window.addEventListener('mousemove', this.resizing);
        },
        /**
         * @desc 结束缩放尺寸
         * @param {Event} e 事件对象
         */
        endResize(e) {
            if (!this.inResizing) {
                return;
            }
            window.removeEventListener('mousemove', this.resizing);
            this.inResizing = false;
            this.$store.commit('RESIZE_SELECTED_COMPONENTS', {
                left: parseInt(this.$el.style.left),
                top: parseInt(this.$el.style.top),
                width: parseInt(this.$el.style.width),
                height: parseInt(this.$el.style.height)
            });
        },
        /**
         * @desc 缩放选中组件尺寸
         * @param {Event} e 事件对象
         */
        resizing(e) {
            if (!this.inResizing) {
                return;
            }
            let translateX = e.pageX - this.resizeStartPosition.left;
            let translateY = e.pageY - this.resizeStartPosition.top;
            switch (this.resizingDirection) {
                case 'left':
                    if (translateX < this.width) {
                        this.$el.style.left = (this.left - 0 + translateX) + 'px';
                        this.$el.style.width = (this.width - translateX) + 'px';
                    } else {
                        this.$el.style.left = (this.left - 0 + (translateX < this.width ? translateX : this.width)) + 'px';
                        this.$el.style.width = '0px';
                    }
                    break;
                case 'right':
                    this.$el.style.width = ((this.width - 0 + translateX) < 0 ? 0 : (this.width - 0 + translateX)) + 'px';
                    break;
                case 'top':
                    if (translateY < this.height) {
                        this.$el.style.top = (this.top - 0 + translateY) + 'px';
                        this.$el.style.height = (this.height - 0 - translateY) + 'px';
                    } else {
                        this.$el.style.top = (this.top - 0 + (translateY < this.height ? translateY : this.height)) + 'px';
                        this.$el.style.height = '0px';
                    }
                    break;
                case 'bottom':
                    this.$el.style.height = ((this.height - 0 + translateY) < 0 ? 0 : (this.height - 0 + translateY)) + 'px';
                    break;
                case 'left-top':
                    if (translateX < this.width) {
                        this.$el.style.left = (this.left -0  + translateX) + 'px';
                        this.$el.style.width = (this.width - 0 - translateX) + 'px';
                    } else {
                        this.$el.style.left = (this.left - 0 + (translateX < this.width ? translateX : this.width)) + 'px';
                        this.$el.style.width = '0px';
                    }
                    if (translateY < this.height) {
                        if (e.shiftKey) { //SHIFT键激状态下宽高等比缩放
                            this.$el.style.height = Math.floor(this.height * (parseInt(this.$el.style.width) / this.width)) + 'px';
                            this.$el.style.top = this.top - (parseInt(this.$el.style.height) - this.height) + 'px';
                        } else {
                            this.$el.style.top = (this.top - 0 + translateY) + 'px';
                            this.$el.style.height = (this.height - translateY) + 'px';
                        }
                    } else {
                        this.$el.style.top = (this.top - 0 + (translateY < this.height ? translateY : this.height)) + 'px';
                        this.$el.style.height = '0px';
                    }
                    break;
                case 'right-top':
                    this.$el.style.width = ((this.width - 0 + translateX) < 0 ? 0 : (this.width - 0  + translateX)) + 'px';
                    if (e.shiftKey) {
                        this.$el.style.height = Math.floor(this.height * (parseInt(this.$el.style.width) / this.width)) + 'px';
                        this.$el.style.top = this.top - (parseInt(this.$el.style.height) - this.height) + 'px';
                    } else {
                        this.$el.style.height = ((this.height - translateY) < 0 ? 0 : (this.height - translateY)) + 'px';
                        this.$el.style.top = (this.top - 0 + (translateY < this.height ? translateY : this.height)) + 'px';
                    }
                    break;
                case 'left-bottom':
                    this.$el.style.left = (translateX > this.width ? ( (this.left - 0) + (this.width - 0) ) : ((this.left - 0) + translateX)) + 'px';
                    this.$el.style.width = ((this.width - translateX) < 0 ? 0 : (this.width - translateX)) + 'px';
                    if (e.shiftKey) {
                        this.$el.style.height = Math.floor(this.height * (parseInt(this.$el.style.width) / this.width)) + 'px';
                    } else {
                        this.$el.style.height = ((this.height - 0 + translateY) < 0 ? 0 : (this.height - 0 + translateY)) + 'px';
                    }
                    break;
                case 'right-bottom':
                    this.$el.style.width = ((this.width - 0 + translateX) < 0 ? 0 : (this.width - 0 + translateX)) + 'px';
                    if (e.shiftKey) {
                        this.$el.style.height = this.$el.style.height = Math.floor(this.height * (parseInt(this.$el.style.width) / this.width)) + 'px';
                    } else {
                        this.$el.style.height = ((this.height - 0 + translateY) < 0 ? 0 : (this.height - 0 + translateY)) + 'px';
                    }
                    break;
                default:
                    void 0;
            };
            this.$emit('onComponentResizing',{
            	width:parseInt(this.$el.style.width),
            	height:parseInt(this.$el.style.height)
            });

            let styleTop = this.$el.style.top;
            let styleLeft = this.$el.style.left;
            document.querySelector('#selected-components-refer-x').style.left = styleLeft;
            document.querySelector('#selected-components-refer-y').style.top = styleTop;
            document.querySelector('#selected-components-refer-position').style.left = styleLeft;
            document.querySelector('#selected-components-refer-position').style.top = styleTop;
            document.querySelector('#selected-components-refer-position').innerHTML = `(${parseInt(styleLeft)}, ${parseInt(styleTop)})`;
        }
    },
    computed: {
        layer_length: function() {
            return this.$store.getters.layer_length;
        },
        isSelected: function() {
            return this.$store.state.selectedIds.find(selectedId => selectedId === this.id)
        }
    },
    watch: {
        isSelected: function(value, oldValue) {
            if (!this.locked) {
                toggleSelectedComponent(this);
            }
        },
        locked: function(value, oldValue) {
            if (value === true) {
                deleteSelectedComponent(this);
            } else {
                toggleSelectedComponent(this);
            }
        },
        width:function(value,oldValue){
        	this.$emit('onComponentResizing',{
            	width:value,
            	height:this.height
            });
        },
        height:function(value,oldValue){
        	this.$emit('onComponentResizing',{
            	width:this.width,
            	height:value
            });
        }
    },
    mounted() {
        this.inResizing = false;
        this.resizeStartPosition = {
            left: this.left,
            top: this.top
        };
        if (this.isSelected && !this.locked) {
            toggleSelectedComponent(this);
        }
        this.$on('onCancelEdit',()=>{
            this.editable = false;
        });
        window.addEventListener('mouseup', this.endResize);
    },
    destroyed() {
        deleteSelectedComponent(this);
    }
}