/****************************************************************
**size  => 组件拖入画布默认尺寸
**properties => 组件属性及默认值
****************************************************************/
export default {
	size:{
		width:200, 
		height:32
	},
	events:['change'],
	properties:[{
		label:'背景颜色',
		props:'backgroundColor',
		type:'colorPicker',
		value:'#ffffff',
		validate:null
	},{
		label:'背景图片',
		props:'backgroundImage',
		type:'colorPicker',
		value:'#ffffff',
		validate:null
	},{
		label:'是否显示边框',
		props:'borderStyle',
		type:'switch',
		value:"solid",
		options:[{
			label:'显示',
			value:'solid'
		},{
			label:'不显示',
			value:'none'
		}]
	},{
		label:'边框大小',
		props:'borderWidth',
		type:'input',
		value:0,
		validate:null
	},{
		label:'边框颜色',
		props:'borderColor',
		type:'colorPicker',
		value:"#000000",
		validate:null
	},{
		label:'内容',
		props:'content',
		type:'input',
		value:'Text',
		validate:null
	},{
		label:'字体大小',
		props:'fontSize',
		type:'number',
		value:14,
		validate:null
	},{
		label:'字体颜色',
		props:'color',
		type:'colorPicker',
		value:'#000000',
		validate:null
	},{
		label:'字体粗细',
		props:'fontWeight',
		type:'select',
		value:'normal',
		options:[{
			label:'正常',
			value:'normal'
		},{
			label:'加粗',
			value:'bold'
		}]
	},{
		label:'文字缩进',
		props:'textIndent',
		type:'input',
		value:0,
		validate:null
	},{
		label:'对齐方式',
		props:'textAlign',
		type:'select',
		value:'left',
		options:[{
			label:'左对齐',
			value:'left'
		},{
			label:'居中对齐',
			value:'center'
		},{
			label:'右对齐',
			value:'right'
		}]
	},{
		label:'溢出隐藏',
		props:'overflow',
		type:'switch',
		value:'hidden',
		options:[{
			label:'隐藏',
			value:'hidden'
		},{
			label:'显示',
			value:'default'
		}]
	},{
		label:'是否发光',
		props:'isTextShadow',
		type:'switch',
		value:false,
		options:[{
			label:'发光',
			value:true,
		},{
			label:'不发光',
			value:false
		}]
	},{
		label:'发光颜色',
		props:'textShadowColor',
		type:'colorPicker',
		value:'#ff0000',
		validate:null
	},{
		label:'发光大小',
		props:'textShadowSize',
		type:'input',
		value:5,
		validate:null
	},{
		label:'垂直对齐',
		props:'alignItems',
		type:'select',
		value:'center',
		options:[{
			label:'居中对齐',
			value:'center'
		},{
			label:'顶部对齐',
			value:'flex-start'
		},{
			label:'底部对齐',
			value:'flex-end'
		}]
	},{
		label:'是否开启阴影',
		props:'isBoxShadow',
		type:'switch',
		value:false,
		options:[{
			label:'开启',
			value:true
		},{
			label:'关闭',
			value:false
		}]
	},{
		label:'发光大小',
		props:'boxShadowSize',
		type:'input',
		value:5,
		validate:null
	},{
		label:'发光颜色',
		props:'boxShadowColor',
		type:'colorPicker',
		value:'#000000',
		validate:null
	}]
}