let cloneElem = null;
let componentType = 'unknown';
let position = {
	pageX:0,
	pageY:0
};
let mouseupCallBacks = [];
/**
 * @desc 克隆节点
 * @param {Element} elem 被克隆的元素
 * @param {Event} event 克隆时的事件
 * @param {String} type 克隆元素的组件类型
 * @returns {undefined} 无返回值
 */
export function cloneElemNode(elem,event,type){
	let {pageX,pageY} = event;
	const {width,height} = elem.getBoundingClientRect();
	componentType = type;
	cloneElem = elem.cloneNode(true);
	position = {
		pageX,
		pageY
	};
	cloneElem.style['pointer-events'] = 'none';
	cloneElem.style.display = 'block';
	cloneElem.style.position = 'absolute';
	cloneElem.style.left = pageX + 'px';
	cloneElem.style.top = pageY + 'px';
	cloneElem.style.width = width + 'px';
	cloneElem.style.height = height + 'px';
	cloneElem.style.color = '#ffffff';
	document.body.appendChild(cloneElem);
	window.addEventListener('mousemove',mousemove);
	window.addEventListener('mouseup',mouseup);
};
/**
 * @desc 获取克隆元素的位置
 * @returns {Object{pageX,pageY}} 位置信息
 */
export function getCloneElemPosition(){
	return Object.assign({},position)
};
/**
 * @desc 克隆元素是否存在，用于设计器来判断是否需要新添一个组件
 */
export function hasCloneElem(){
	return cloneElem === null;
};
/**
 * @desc 获取克隆元素的组件类型
 * @returns {String} 组件类型
 */
export function	getComponentType(){
	return componentType;
};
/**
 * @desc 添加鼠标松开时的回调 
 * @returns {undefined} 无返回值
 */
export function addMouseupCallBack(fn){
	if(typeof fn === 'function'){
		mouseupCallBacks.push(fn)
	}
};

function mousemove(event){
	event.returnValue = false;
	event.preventDefault();
	var {pageX,pageY} = event;
	position = {
		pageX,
		pageY
	};
	cloneElem.style.left = pageX + 'px';
	cloneElem.style.top = pageY + 'px';
}

function mouseup(event){
	if(cloneElem === null){
		return;
	}
	var {pageX,pageY} = event;
	position = {
		pageX,
		pageY
	};
	cloneElem.style.left = pageX + 'px';
	cloneElem.style.top = pageY + 'px';
	document.body.removeChild(cloneElem);
	cloneElem = null;
	window.removeEventListener('mousemove',mousemove);
	mouseupCallBacks.forEach(callBack=>{
		callBack(position,componentType)
	});
}

