import Vue from "vue";
import Vuex from "vuex";
import cloneDeep from 'lodash/cloneDeep';
import {
    isOverlap
} from './utils/index.js';
import {
    createRecorder,
    revertState,
    forwardState
} from '@/services/recordState.js';
Vue.use(Vuex);
let store = new Vuex.Store({
    state: {
        canvas:{
            width:1920,
            height:1080
        },
        isDesign: true,
        ctrl: false,
        shift: false,
        referenceLine: {
            show: true, //是否展示参考线
            referenceLineX: [], //横向参考线
            referenceLineY: [] //纵向参考线
        },
        pasteOffset:{
            left:10,
            top:10
        },
        scale:1,
        components: Array(0).fill().map((component, idx) => {
            return {
                id: Math.random().toString(32).slice(2),
                name: Math.random().toString(32).slice(2),
                locked: Math.random() > 0.5,
                visible:true,
                width: Math.floor(Math.random() * 200),
                height: Math.floor(Math.random() * 400),
                top: Math.floor(Math.random() * 600),
                left: Math.floor(Math.random() * 1800),
                backgroundColor: `rgba(${Math.floor(Math.random()*255)},${Math.floor(Math.random()*255)},${Math.floor(Math.random()*255)},${Math.random()})`
            }
        }),
        // components:[],
        selectedIds: [], //当前所有选中组件的ID
        copyIds: [], //复制剪切版的组件ID
        groups: {
            currentGroup: '',
            list: [
                // {
                //   name:'group_1',
                //   components:[]
                // }
            ] //所有分组
        }
    },
    getters: {
        //组件数量
        layer_length: (state) => {
            return state.components.length;
        },
        //选中组件信息(只针对有一个可编辑组件)
        selectedComponentInfo:(state)=>{
            if(state.selectedIds.length<1){
                return {
                    show:false,
                    index:-1
                }
            }else{
                let selectedComponents = state.components.filter(component=>state.selectedIds.some(id=>component.id === id && !component.locked))
                if(selectedComponents.length > 1){
                    return {
                        show:false,
                        index:-1,
                        config:{
                            
                        }
                    }
                }else{
                    let index = -1;
                    for(let i=0;i<state.components.length;i++){
                        if(state.components[i].id === selectedComponents[0].id){
                            index = i;
                            break;
                        }
                    }
                    return{
                        show:true,
                        index,
                        config:selectedComponents[0]
                    }
                }
            }
        },
        //是否显示组合操作
        /*showGroup:(state)=>{
          return state.selectedIds.length > 1;
        },*/
        //单组件尺寸缩放框
        resizerHelper: (state) => {
            let selectedComponents = state.components.filter(component => state.selectedIds.some(id => component.id === id && !component.locked));
            if (selectedComponents.length === 1) {
                let {
                    left,
                    top,
                    width,
                    height
                } = selectedComponents[0];
                return {
                    show: true,
                    left,
                    top,
                    width,
                    height
                }
            } else {
                return {
                    show: false,
                    left: 0,
                    top: 0,
                    width: 0,
                    height: 0
                }
            }
        },
        //组合操作框
        selectedGroup: (state) => {
            if (state.selectedIds.length < 2) {
                return {
                    show: false,
                    left: 0,
                    top: 0,
                    width: 0,
                    height: 0
                }
            } else {
                let selectedComponents = state.components.filter(component => state.selectedIds.some(id => component.id === id && !component.locked));
                let left = Math.min.apply(null, selectedComponents.map(component => component.left)) - 1;
                let top = Math.min.apply(null, selectedComponents.map(component => component.top)) - 1;
                let width = Math.max.apply(null, selectedComponents.map(component => Math.floor(component.left) + Math.floor(component.width))) - left + 1;
                let height = Math.max.apply(null, selectedComponents.map(component => Math.floor(component.top) + Math.floor(component.height))) - top + 1;
                return {
                    show: selectedComponents.length > 1 ? true : false,
                    left,
                    top,
                    width,
                    height
                }
            }
        },
    },
    mutations: {
        /**
         * @desc 缩小画布
         */
        SET_CANVAS_SCALE_NARROW(state){
            if(state.scale <= .2){
                return;
            }
            state.scale -= 0.1;
        },
        /**
         * @desc 放大画布
         */
        SET_CANVAS_SCALE_ENLARGE(state){
            state.scale += 0.1;
        },
        /**
         * @desc 重置画布缩放
         */
        RESET_CANVAS_SCALE(state){
            state.scale = 1;
        },
        /**
         * @desc 缩放画布至
         */
        SCALE_CANVAS_TO(state,payload){
            if(typeof payload !== 'number'|| isNaN(payload)){
                return;
            }
            state.scale = payload;
        },
       /**
        * @desc 初始化页面数据
        */
        INIT_STATE(state,payload){
          // state = payload;
          Object.assign(state,payload);
        },
        /**
         * 设置组件属性
         * @param {*} state 
         * @param {*} payload  
         */
        SET_COMPONENT_PROPS(state,payload){
            let component = state.components.find(component=>component.id === state.selectedIds[0]);
            component.config = Object.assign(component.config,payload);
        },
        /**
         * 
         * @param {*} state 
         * @param {Object{left,top}} payload 偏移值
         */
        SET_PASTE_OFFSET(state,payload){
            state.pasteOffset = payload;
        },
        /**
         * 设置组件层级向上
         * @param {*} state 
         */
        SET_COMPONENT_LAYER_UP(state){
            if(state.selectedIds.length !== 1){
                return;
            }
            let index = 0;
            for(let i=0;i<state.components.length;i++){
                if(state.components[i].id === state.selectedIds[0]){
                    index = i;
                    break;
                }
            };
            if(index>0 && index < (state.components.length - 1)){
                let pre_component = state.components[index-1];
                let current_component = state.components[i];
                state.components[index] = pre_component;
                state.components[index-1] = current_component;
            }
        },
        /**
         * 设置组件层级向下
         * @param {*} state 
         */
        SET_COMPONENT_LAYER_DOWN(state){
            if(state.selectedIds.length !== 1){
                return;
            }
            let index = 0;
            for(let i=0;i<state.components.length;i++){
                if(state.components[i].id === state.selectedIds[0]){
                    index = i;
                    break;
                }
            };
            if(index < (state.components.length - 1) && index > 0){
                let pre_component = state.components[index-1];
                let current_component = state.components[i];
                state.components[index] = pre_component;
                state.components[index-1] = current_component;
            }
        },
        /**
         * @desc 偏移选中组件
         */
        TRANSLATE_SELECTED_COMPONENTS(state, payload) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            components.forEach(component => {
                component.left += payload.translateX;
                component.top += payload.translateY;
            });
        },
        /**
         * @desc 等比缩放选中组件的尺寸
         */
        SCALE_SELECTED_COMPONENTS(state, rate) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            let minLeft = Math.min.apply(null, components.map(component => component.left));
            let maxLeft = Math.max.apply(null, components.map(component => component.left));
            let minTop = Math.min.apply(null, components.map(component => component.top));
            let maxTop = Math.max.apply(null, components.map(component => component.top));
            let componentsRelative = components.map(component => {
                return {
                    left: component.left - minLeft,
                    top: component.top - minTop
                }
            });
            components.forEach((component, idx) => {
                component.width = Math.floor((component.width * rate));
                component.height = Math.floor((component.height * rate));
                component.left = Math.floor(minLeft + (componentsRelative[idx].left * rate));
                component.top = Math.floor(minTop + (componentsRelative[idx].top * rate));
            });
        },
        SET_KEY_CTRL_TRUE(state) {
            state.ctrl = true;
        },
        SET_KEY_CTRL_FALSE(state) {
            state.ctrl = false;
        },
        SET_KEY_SHIFT_TRUE(state) {
            state.shift = true;
        },
        SET_KEY_SHIFT_FALSE(state) {
            state.shift = false;
        },
        /**
         * @desc 清空选中组件列表
         **/
        EMPTY_SELECTED_COMPONENTS(state) {
            state.selectedIds = [];
            state.groups.currentGroup = '';
        },
        /**
         * @desc 重新单选一个组件
         */
        RESET_SELECTED_COMPONENTS(state, payload) {
            // state.selectedIds = [payload.id];
            let group = state.groups.list.find(group => group.components.find(component => component === payload.id));
            if (group) {
                state.groups.currentGroup = group.name;
                state.selectedIds = group.components;
            } else {
                state.groups.currentGroup = '';
                state.selectedIds = [payload.id];
            }
        },
        /**
         * @desc 添加选中组件
         */
        ADD_SELECTED_COMPONENTS(state, payload) {
            if (!state.selectedIds.find(selectedId => selectedId === payload.id)) {
                state.selectedIds.push(payload.id);
            }
        },
        /**
         * @desc 切换选中组件状态(多选时已选中组件设置为非选中状态)
         */
        TOGGLE_SELECTED_COMPONENTS(state, payload) {
            if (state.selectedIds.find(selectedId => selectedId === payload.id)) {
                state.selectedIds = state.selectedIds.filter(selectedId => selectedId !== payload.id)
            } else {
                let group = state.groups.list.find(group => group.components.find(component => component === payload.id));
                if (group) {
                    state.groups.currentGroup = group.name;
                    group.components.forEach(component => {
                        state.selectedIds.push(component);
                    });
                } else {
                    state.groups.currentGroup = '';
                    state.selectedIds.push(payload.id);
                }
            }
        },
        /**
         * @desc 选中所有组件
         */
        SELECTED_ALL_COMPONENTS(state) {
            state.selectedIds = state.components.map(component => component.id);
        },
        /**
         * @desc 通过框选范围选中组件
         */
        SELECTED_COMPONENTS_BY_HELPER(state, payload) {
            state.selectedIds = state.components.filter(component => !component.locked).filter(component => {
                return isOverlap(component, payload);
            }).map(component => component.id);
        },
        /**
         * @desc 添加组件
         * @param {Object{width,height,left,top}} payload 组件的位置及宽度信息 
         */
        ADD_COMPONENT(state, payload) {
            state.components.push( Object.assign({
                id: Math.random().toString(32).slice(2),
                name: payload.type + '-' + Math.random().toString(32).slice(2),
                locked: false,
                visible:true,
                backgroundColor: `transparent`
            },payload));
        },
        /**
         * @desc 删除选中组件
         */
        DELETE_COMPONENT(state) {
            for (let i = 0; i < state.selectedIds.length; i++) {
                for (let j = 0; j < state.components.length; j++) {
                    if (state.selectedIds[i] === state.components[j].id && !state.components[j].locked) {
                        state.components.splice(j, 1);
                        break;
                    }
                }
            }
            this.commit('EMPTY_SELECTED_COMPONENTS');
        },
        /**
         * @desc 更新组件信息
         * @param {String} id 组件ID
         * @param {Object} config 组件的配置信息
         */
        UPDATE_COMPONENT(state, id, config) {},
        /**
         * @desc 复制选中组件
         */
        COPY_COMPONENTS(state) {
            state.copyIds = state.selectedIds;
        },
        /**
         * @desc 粘贴选中组件
         */
        PASTE_COMPONENTS(state) {
            let selectedComponents = state.components.filter(component => state.copyIds.some(id => component.id === id));
            let pastedComponents = cloneDeep(selectedComponents);
            this.commit('EMPTY_SELECTED_COMPONENTS');
            pastedComponents.forEach(component => {
                let componentName = component.name.split('_');
                let pasteReg = /^copy(\d+)$/;
                component.id = Math.random().toString(32).slice(2);
                component.name = componentName.length === 1 ? componentName[0]+'_copy1' : componentName[0]+`${!pasteReg.test(componentName[1]) ? '_copy1' : '_copy'+ (parseInt(componentName[1].replace(/[^\d]/g,''))+1)}`;
                component.left += state.pasteOffset.left;
                component.top += state.pasteOffset.top;
                state.components.push(component);
                state.selectedIds.push(component.id);
            });
            this.commit('COPY_COMPONENTS');
        },
        /**
         * @desc CTRL+Z
         */
        CTRL_Z() {
            revertState();
        },
        /**
         * @desc CTRL+Y 
         */
        CTRL_Y() {
            forwardState();
        },
        /**
         * @desc 组合组件
         */
        GROUP_COMPONENTS(state) {
            let groupLength = state.groups.list.length;
            state.groups.list.push({
                name: `group_${groupLength+1}`,
                components: [...state.selectedIds]
            });
        },
        /**
         * @desc 解除组合
         */
        UNGROUP_COMPONENTS(state, groupName) {
            let currentGroup = state.groups.currentGroup;
            state.groups.currentGroup = '';
            state.groups.list = state.groups.list.filter(item => item.name !== currentGroup);
        },
        /**
         * @desc 修改分组名称
         */
        CHANGE_GROUPNAME(groupid, name) {},
        /**
         * @desc 修改分组里的组件
         */
        CHANGE_GROUP(groupid, selectedIds) {},
        /**
         * @desc 左移选中组件
         */
        MOVE_SELECTED_COMPONENTS_LEFT(state) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            components.forEach(component => {
                component.left -= 1;
            });
        },
        /**
         * @desc 右移选中组件
         */
        MOVE_SELECTED_COMPONENTS_RIGHT(state) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            components.forEach(component => {
                component.left += 1;
            });
        },
        /**
         * @desc 上移选中组件
         */
        MOVE_SELECTED_COMPONENTS_TOP(state) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            components.forEach(component => {
                component.top -= 1;
            });
        },
        /**
         * @desc 下移选中组件
         */
        MOVE_SELECTED_COMPONENTS_BOTTOM(state) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            components.forEach(component => {
                component.top += 1;
            });
        },
        /**
         * @desc 左对齐
         */
        ALIGN_SELECTED_COMPONENTS_LEFT(state) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            let min = Math.min.apply([], components.map(component => component.left));
            components.forEach(component => {
                component.left = min;
            });
        },
        /**
         * @desc 右对齐
         */
        ALIGN_SELECTED_COMPONENTS_RIGHT(state) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            let max = Math.max.apply([], components.map(component => Number(component.left) + Number(component.width)));
            components.forEach(component => {
                component.left = max - component.width;
            });
        },
        /**
         * @desc 顶部对齐
         */
        ALIGN_SELECTED_COMPONENTS_TOP(state) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            let top = Math.min.apply([], components.map(component => component.top));
            components.forEach(component => {
                component.top = top;
            });
        },
        /**
         * @desc 底部对齐
         */
        ALIGN_SELECTED_COMPONENTS_BOTTOM(state) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            let max = Math.max.apply([], components.map(component => Number(component.top) + Number(component.height)));
            //let bottom = Math.max.apply([],components.map(component=>component.top));
            components.forEach(component => {
                component.top = max - component.height;
            });
        },
        /**
         * @desc 锁定选中组件
         * @returns {undefined} 无返回值
         */
        LOCK_SELECTED_COMPONENTS(state) {
            state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).forEach(component => {
                component.locked = true;
            });
        },
        /**
         * @desc 解锁选中组件
         * @returns {undefined} 无返回值
         */
        UNLOCK_SELECTED_COMPONENTS(state) {
            state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).forEach(component => {
                component.locked = false;
            });
        },
        /**
         * @desc 隐藏选中组件
         * @returns {undefined} 无返回值
         */
        HIDE_SELECTED_COMPONENTS(state){
            state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).forEach(component => {
                component.visible = false;
            });
        },
        /**
         * @desc 隐藏指定ID组件
         * @param {Object{id}} 组件ID
         * @returns {undefined} 无返回值
         */
        HIDE_COMPONENTS_BY_ID(state,payload){
            state.components.find(component=>component.id === payload.id).visible = false;
        },
        /**
         * @desc 显示指定ID组件
         * @param {Object{id}} 组件ID
         * @returns {undefined} 无返回值
         */
        SHOW_COMPONENTS_BY_ID(state,payload){
            state.components.find(component=>component.id === payload.id).visible = true;
        },
        /**
         * @desc 显示选中组件
         * @returns {undefined} 无返回值
         */
        SHOW_SELECTED_COMPONENTS(state){
            state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).forEach(component => {
                component.visible = true;
            });
        },

        /**
         * @desc 缩放单一选中的组件尺寸
         * @param {Object{left,top,width,height}} payload 新的定位及尺寸
         * @returns {undefined} 无返回值
         */
        RESIZE_SELECTED_COMPONENTS(state, payload) {
            let components = state.components.filter(component => state.selectedIds.find(selectedId => selectedId === component.id)).filter(component => !component.locked);
            if (components.length === 1) {
                let {
                    left,
                    top,
                    width,
                    height
                } = payload;
                components[0].left = left;
                components[0].top = top;
                components[0].width = width;
                components[0].height = height;
            }
        }
    },
    actions: {},
    modules: {}
});
createRecorder(store);
export default store;