import Vue from 'vue'
import cloneDeep from 'lodash/cloneDeep'
import isEqual from 'lodash/isEqual'
let stateStack = [];
let stateRecorder = null;
let ctrl_z = [];
let store = null;
const RECORD_STEPS = 50;
/**
 * @desc 创建状态记录器
 */
export function createRecorder(storeInstance) {
    store = storeInstance;
    let commit = store.commit;
    store.commit = function() {
        commit.apply(store, arguments);
        if (['CTRL_Z', 'CTRL_Y'].find(ctrl => ctrl === arguments[0])) {
            return;
        }
        recordState({
            type: arguments[0],
            state: store.state
        });
    }
    stateStack.push({
        type: 'INIT_STATE',
        state: cloneDeep(storeInstance.state)
    });
}
/**
 * @desc 记录状态变更
 */
export function recordState(state) {
    if (isEqual(state, stateStack[stateStack.length - 1])) {
        return;
    }
    if (stateStack.length < RECORD_STEPS) {
        stateStack.push(JSON.parse(JSON.stringify(state)));
    } else {
        stateStack.shift();
        stateStack.push(JSON.parse(JSON.stringify(state)));
    }
}
/**
 * @desc CTRL+Z
 */
export function revertState() {
    let lastState = stateStack[stateStack.length - 1 - 1];
    if (lastState === undefined) {
        return;
    }
    stateStack.pop();
    if (ctrl_z.length < RECORD_STEPS) {
        ctrl_z.push(lastState);
    } else {
        ctrl_z.shift();
        ctrl_z.push(lastState);
    }
    store.replaceState(lastState.state);
}
/**
 * @desc CTRL+Y
 */
export function forwardState() {
    if (ctrl_z.length < 1) {
        return;
    }
    let lastState = ctrl_z.pop();
    if (lastState === undefined) {
        return;
    } else {
        if (stateStack.length < RECORD_STEPS) {
            stateStack.push(cloneDeep(lastState));
        } else {
            stateStack.shift();
            stateStack.push(cloneDeep(lastState));
        }
        store.replaceState(lastState.state);
    }
}